* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  padding: 1rem;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 2rem;
  padding-bottom: 0.5rem;
}

input {
  padding: 0.5rem;
  border-radius: 0.5rem;
  border: 2px solid gray;
  margin: 0.25rem 0;
}

ul {
  list-style-type: none;
}

li {
  padding: 0.75rem;
}

li:nth-child(odd) {
  background: #eee;
}

button {
  cursor: pointer;
}

.add-form {
  background: rgb(226, 226, 226);
  width: fit-content;
  padding: 1rem;
  border-radius: 1rem;
  margin: 1rem 0;
}

.add-form input {
  width: 100%;
}

.add-form button {
  padding: 0.5rem 2rem;
  font-weight: bold;
  border-radius: 0.5rem;
  margin-top: 0.5rem;
  background-color: rgb(214, 186, 45);
}

.add-form button:hover {
  background-color: rgb(152, 132, 27);
}

.list-title {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin: 2rem 0 1rem 0;
}

.delete-btn {
  padding: 0.25rem;
  border-radius: 0.25rem;
  border: 2px solid rgb(224, 86, 86);
  background: rgb(255, 152, 152);
  margin-left: auto;
}

.delete-btn:hover {
  background: rgb(224, 86, 86);
}

.person-details {
  display: flex;
}

.notif {
  color: green;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.error {
  color: red;
}
